import Vue from 'vue';
import Vuex from 'vuex';
import axios from 'axios';
import VueRouter from 'vue-router';
import Order from './views/order.vue';

import tpl from './views/test.tpl';
console.log("----------------", tpl);
Vue.use(Vuex);
Vue.use(VueRouter);
var Foo = {
    template: '<div class="nav">foo</div>'
};
var Bar = {
    template: '<div class="nav">bar</div>'
};
var User = {
    template: '<div class="user nav">\n                <h2>User {{ $route.params.id }}</h2>\n                <router-link to="/user/415/profile">User Profile</router-link>\n                <router-link to="/user/425/posts">User posts</router-link>\n                <router-view></router-view>\n            </div>'
};
var UserHome = { template: '<div>Home</div>' };
var UserProfile = { template: '<div>Profile</div>' };
var UserPosts = { template: '<div>Posts</div>' };

var routes = [{
    path: '/foo',
    component: Foo
}, {
    path: '/order/:id',
    name: 'order',
    component: Order,
    beforeEnter: function beforeEnter(to, from, next) {
        console.log('组件内部路由');
        next();
    }
}, {
    path: '/bar',
    component: Bar
}, {
    path: '/user/:id',
    component: User,
    children: [{
        path: '',
        component: UserHome
    }, {
        path: 'profile',
        component: UserProfile
    }, {
        path: 'posts',
        component: UserPosts
    }]
}];

var router = new VueRouter({
    routes: routes });
router.beforeEach(function (to, from, next) {
    console.log('全局路由');
    next();
});

var app = new Vue({
    data: function data() {
        return {
            transitionName: 'fade'
        };
    },

    router: router,
    template: '\n        <div id="app">\n        <p>Hello App!</p>\n        <p>\n            <router-link to="/foo">Go to Foo</router-link>\n            <router-link to="/bar">Go to Bar</router-link>\n            <router-link to="/user/11">Go to User</router-link>\n            <router-link :to="{ name: \'order\', params: { id: 010201708281710 }}">Go to Order</router-link>\n        </p>\n        <transition :name="transitionName">\n            <router-view></router-view>\n        </transition>\n    </div>    \n    ',
    watch: {
        '$route': function $route(to, from) {
            var toDepth = to.path.split('/').length;
            var fromDepth = from.path.split('/').length;
            this.transitionName = toDepth < fromDepth ? 'slide-fade' : 'fade';
        }
    }
}).$mount('#app');


